@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;700&family=Roboto+Mono:wght@400;700&display=swap');

.GitHubCard {
    background-color: #212528;
    width: 548px;
    height: 300px;
    border-radius: 16px;
    font-family: 'Roboto Mono';
    font-weight: 400;
    color:  #808082;
}

.TabBar {
    background: #1E1E1E;
    display: flex;
    height: 40px;
    align-items: center;
    gap: 8px;
    padding-left: 16px;
    border-radius: 16px 16px 0 0;
}

.Dot {
    width: 8px;
    height: 8px;
    background: #464A4D;
    border-radius: 50%;
}

.GithubTitle {
    color: #FFFFFF;
    font-weight: 700;
    padding-left: 4px;
}
.GithubIcon {
    margin-left: 32px;
    margin-top: 18px;
    padding: 2px;
}
.GithubText {
    margin-left: 32px;

}
.GithubTextProject {
    margin-right: 253px;
    display: flex;
    justify-content: center;
}
.GithubTextLinks {
    color: #14B5D0;
}
.GithubTextGithub {
    margin-right: 255px;
    display: flex;
    justify-content: center;
    font-size: 16px;
    margin-left: 40px;
    
}
.GithubTextDemo {
    margin-right: 294px;
    display: flex;
    justify-content: center;
    margin-left: 60px;
}
.GithubTextPading {
    padding-left: 4px;
    font-weight: normal;
}